import {Component, OnInit, ViewEncapsulation, HostListener, Inject} from '@angular/core';
import { HeroService } from '../hero.service';
import { cartItemData } from '../hero';
import { Router } from '@angular/router';
import { NgxSpinnerService } from "ngx-spinner";
import Swal from "sweetalert2";


@Component({
  selector: 'app-wishlist',
  templateUrl: './wishlist.component.html',
  styleUrls: ['./wishlist.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class WishlistComponent implements OnInit {

    wishListDataItems: Array<cartItemData> = [];
    priceArray: any = [];
    loadingData:boolean=false;
    promocodeflag:boolean=false;
    totalPrice:number =0;
    public currentUser: any;
    constructor(private heroService: HeroService, private authenticationService: HeroService, private router: Router,private spinner: NgxSpinnerService) {
        this.authenticationService.currentUser.subscribe(x => {
            this.currentUser = x;
            if(!this.currentUser){
                this.router.navigate(['/']);
            }

        });
    }
 @HostListener('window:scroll', [])
  onWindowScroll() {
    if (document.body.scrollTop > 10 ||     
    document.documentElement.scrollTop > 10) {
      document.getElementById('navbarResponsive').classList.remove('show');
      //document.getElementById('paragraph').classList.add('green');
    }
  }
    ngOnInit() {
        //this.spinner.show();

        // console.log(localStorage.getItem('checkoutAray'));
        this.loadingData = true;
        this.loadcart();

    }
    loadcart(){

        this.authenticationService.getcartItemsData()
            .subscribe(
                data => {
                    this.wishListDataItems = data;
                    this.wishListDataItems.forEach(element => {
                        this.priceArray.push(element["total"]);
                    });
                    this.loadingData = false;
                    //this.spinner.hide();
                },
                error => {

                });
    }

    showTotalPrice() {
        this.totalPrice = this.priceArray.reduce(function (acc, val) {
            return acc + val;
        }, 0);
        return this.totalPrice;
    }
    showTax() {
        return this.totalPrice*12/100;
    }

    redirectToCheckout() {
        this.router.navigate(['/checkout']);
    }

    promocode(){
        this.promocodeflag = !this.promocodeflag;
    }
    removeProductFromCart(productinfo) {
        console.log(productinfo);
        if (confirm('Are you sure?') == true) {
            this.loadingData = true;
            this.heroService.removeCartItemsData(productinfo)
                .subscribe(data => {
                    if (data["status"] == '1') {
                        this.priceArray=[];
                        this.authenticationService.getcartItemsData()
                            .subscribe(
                                data => {
                                    this.wishListDataItems = data;
                                    this.wishListDataItems.forEach(element => {
                                        this.priceArray.push(element["total"]);
                                    });
                                    this.loadingData = false;
                                },
                                error => {

                                });
                    } else {
                        Swal.fire('', data["message"], 'error');
                    }

                });
            // this.checkoutArray.push(2);this.checkoutArray.push(3); //remove the line when api integrated

        } else {
            return false;
        }
    }

    addtolightbox(productinfo){
        console.log(productinfo);
        this.loadingData =true;
        this.heroService.addWishListItemsData(productinfo.cart_product_id)
            .subscribe(data => {
                if(data["status"]=='1'){

                    this.heroService.removeCartItemsData(productinfo)
                        .subscribe(data => {
                            if (data["status"] == '1') {
                                this.loadcart();
                                this.loadingData =false;
                                Swal.fire('', "Sucessfully moved to wishlist", 'success');
                                this.priceArray=[];

                           } else {
                                Swal.fire('', data["message"], 'error');
                            }

                        });

                   // this.router.navigate(['/wishlist']);
                }else{
                    this.loadingData =false;
                    Swal.fire('', data["message"], 'error');
                }

            });
    }
}
